@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500;700;900&display=swap);
:root{--text-900: hsl(210, 29%, 10%);--text-800: hsl(210, 20%, 20%);--text-700: hsl(210, 20%, 30%);--text-600: hsl(210, 20%, 40%);--text-500: hsl(210, 20%, 50%);--text-400: hsl(210, 20%, 60%);--text-300: hsl(210, 20%, 70%);--text-200: hsl(210, 20%, 80%);--text-100: hsl(210, 20%, 90%);--theme-900: hsl(216, 89%, 20%);--theme-800: hsl(216, 89%, 28%);--theme-700: hsl(216, 89%, 36%);--theme-600: hsl(216, 89%, 44%);--theme-500: hsl(216, 89%, 52%);--theme-400: hsl(216, 89%, 60%);--theme-300: hsl(216, 89%, 68%);--theme-200: hsl(216, 89%, 76%);--theme-100: hsl(216, 89%, 84%);--depth-900: hsl(265, 20%, 32%);--depth-800: hsl(265, 20%, 40%);--depth-700: hsl(265, 20%, 48%);--depth-600: hsl(265, 20%, 56%);--depth-500: hsl(265, 20%, 64%);--depth-400: hsl(265, 20%, 72%);--depth-300: hsl(265, 20%, 80%);--depth-200: hsl(265, 20%, 88%);--depth-100: hsl(265, 20%, 96%)}[data-theme="dark"]{--depth-900: hsl(265, 50%, 16%);--depth-800: hsl(265, 50%, 24%);--depth-700: hsl(265, 50%, 32%);--depth-600: hsl(265, 50%, 40%);--depth-500: hsl(265, 50%, 48%);--depth-400: hsl(265, 50%, 56%);--depth-300: hsl(265, 50%, 64%);--depth-200: hsl(265, 50%, 72%);--depth-100: hsl(265, 50%, 80%)}html,body,header,nav,main,aside,section,article,figure,figcaption,footer,div,h1,h2,h3,h4,h5,h6,a,p,span,ul,li,ol,i,address,strong,form,fieldset,input,legend,button,summary,details,select,option,label,img,svg{margin:0;padding:0;border:none;color:hsl(210, 20%, 20%);color:var(--text-800);font-family:"Noto Sans KR", sans-serif;font-weight:300;line-height:1;letter-spacing:-0.03vw;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box;word-break:keep-all}::selection,::-moz-selection{background-color:hsl(216, 89%, 52%);background-color:var(--theme-500);color:#fff}:root{--inner-padding: 2rem}header,nav,main,aside,section,article,footer{display:block}h1,h2,h3,h4,h5,h6,strong,b{font-weight:700}html{font-size:0.625em;background-color:hsl(265, 20%, 96%);background-color:var(--depth-100)}a{text-decoration:none;color:inherit;cursor:default}i{font-style:normal}button{border-radius:0;background-color:transparent;cursor:default}ul,li{list-style:none}img{vertical-align:middle}summary{list-style:none}summary::-webkit-details-marker{display:none}*:focus{outline:none}*::-webkit-scrollbar{width:0.5rem}*::-webkit-scrollbar-thumb{background-color:rgba(0,0,0,0.5);border-radius:0.25rem}.line-break{display:block}@media screen and (min-width: 768px){:root{--inner-padding: 5rem}}@media screen and (min-width: 1024px){a,button{cursor:pointer}}

